<template>
  <div class="meal-data-entry">
    <span
      :class="{ transparent: selectedTab === 7}"
      class="main-view-header"
      dir="rtl"
    >{{ studentReportData.firstName }} {{ studentReportData.lastName }} | {{ $t('overall') }} {{ aggregatedScore }} {{ $t('mealDataEntry.points') }}</span>
    <div class="main-tabs-and-data-div">
      <div class="tabs">
        <div
          class="tab"
          :class="[selectedTab === 1 ? 'selected-tab' : '']"
          @click="setSelectedTab(1)"
        >
          <span id="tab-num">1</span>
          <span id="title">{{ $t('mealDataEntry.titles.1') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 2 ? 'selected-tab' : '', !didBringFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(2)"
        >
          <span id="tab-num">2</span>
          <span id="title">{{ $t('mealDataEntry.titles.2') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 3 ? 'selected-tab' : '', !didBringFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(3)"
        >
          <span id="tab-num">3</span>
          <span id="title">{{ $t('mealDataEntry.titles.3') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 4 ? 'selected-tab' : '', !didBringFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(4)"
        >
          <span id="tab-num">4</span>
          <span id="title">{{ $t('mealDataEntry.titles.4') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 5 ? 'selected-tab' : '', !didBringFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(5)"
        >
          <span id="tab-num">5</span>
          <span id="title">{{ $t('mealDataEntry.titles.5') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 6 ? 'selected-tab' : '', !didBringFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(6)"
        >
          <span id="tab-num">6</span>
          <span id="title">{{ $t('mealDataEntry.titles.6') }}</span>
        </div>
        <div
          class="tab"
          :class="[selectedTab === 7 ? 'selected-tab' : '', !didBringFood && !choosedBroughtFood ? 'disabled-tab' : '']"
          @click="setSelectedTab(7)"
        >
          <span id="tab-num">7</span>
          <span id="title">{{ $t('mealDataEntry.titles.7') }}</span>
        </div>
      </div>

      <!-- The tabs content div, with 3 different divs, one for step 1, one for step 2-6, and one for the final step, 7. -->
      <div class="tab-content">
        <div
          v-show="selectedTab === 1"
          class="tab-1 inside-tab-padding"
        >
          <div
            class="main-tab-content-title"
            dir="rtl"
          >{{ studentReportData.firstName }} {{ $t('mealDataEntry.broughtFoodQuestion') }}</div>
          <div
            class="radio-choice-main-div"
            dir="rtl"
          >
            <div
              class="radio-choice-div"
            >
              <img
                :src="didBringFood || broughtFood ? require('@/assets/img/mealdataentry/did-bring-food-selected.png') : require('@/assets/img/mealdataentry/did-bring-food.png')"
                class="radio-choice-pic"
                @click="selectRadioOptionBringFood(true)"
              >
              <span class="radio-choice-option-title">{{ $t('yes') }}</span>
              <span class="radio-choince-option-points">1 {{ $t('mealDataEntry.points') }}</span>
            </div>
            <div class="radio-choice-div">
              <img
                :src="didNotBringFood ? require('@/assets/img/mealdataentry/not-bring-food-selected.png') : require('@/assets/img/mealdataentry/not-bring-food.png')"
                class="radio-choice-pic"
                @click="selectRadioOptionBringFood(false)"
              >
              <span class="radio-choice-option-title">{{ $t('no') }}</span>
              <span class="radio-choince-option-points">0 {{ $t('mealDataEntry.points') }}</span>
            </div>
          </div>
        </div>
        <div
          v-show="selectedTab !== 1 && selectedTab !== 7"
          class="tab-2-6 inside-tab-padding"
        >
          <FoodList :selected-tab="selectedTab" />
        </div>
        <div
          v-if="selectedTab === 7"
          class="tab-7"
        >
          <div
            class="main-tab-content-title"
            dir="rtl"
          >{{ studentReportData.lastName }} {{ studentReportData.firstName }}, {{ $t('mealDataEntry.youWon') }}{{ aggregatedScore }} {{ $t('mealDataEntry.manyPoints') }}</div>
          <div
            class="encouragement-slogan"
          >
            {{ encouragementSlogan }}
          </div> 

          <div class="feedback-image">
            <img :src="require('@/assets/img/mealdataentry/feedback-images/'+encouragementImage)">
          </div>
          <div
            class="improvement-slogan"
          >
            {{ this.$t(improvementSlogan) }}
          </div> 
        </div>
      </div>
    </div>
    <div class="next-btn-container">
      <button
        :class="[!choosedBroughtFood ? 'disabled-btn' : '']"
        :disabled="!choosedBroughtFood"
        @click="nextTab"
      >{{ buttonTitle }}</button>
    </div>
  </div>
</template>

<script>
import FoodList from '../FoodListComponent.vue';
import mealEntryDataLogic from '../../logic/mealEntryDataLogic.js';
import trusteeService from '../../services/trustee-service';
import spinnerService from '../../services/spinner-service';

export default {
  name: 'MealDataEntry',
  components: { FoodList },
  data() {
    return {
      studentReportData: this.$store.getters['trustee/getMealEntryStudentData'],
      selectedTab: 1,
      didBringFood: null,
      didNotBringFood: null,
      stepOneCalculated: false,
      encouragementSlogan: null,
      encouragementImage: null,
      improvementSlogan: null,
      successfulRequest: false
    };
  },
  computed: {
    aggregatedScore() {
      let score = 0;
      if (this.broughtFood) {
        score++;
      } else {
        score = 0;
        this.$store.commit('trustee/setActiveDataEntryScore', score);
        return score;
      }

      let itemsSelectedCategories = this.studentReportData.items;

      for (let category in itemsSelectedCategories) {
        let currentCat = itemsSelectedCategories[category];
        for (let item in currentCat) {
          score = score + currentCat[item].itemScore;
        }
      }
      this.$store.commit('trustee/setActiveDataEntryScore', score);
      return score;
    },
    selectedSomeItems() {
      let itemsSelectedCategories = this.studentReportData.items;

      for (let category in itemsSelectedCategories) {
        let currentCat = itemsSelectedCategories[category];
        if (currentCat.length > 0) {
          return true;
        }
      }
      return false;
    },
    choosedBroughtFood() {
      if (
        (this.didBringFood && !this.didNotBringFood) ||
        (!this.didBringFood && this.didNotBringFood)
      ) {
        return true;
      }
      return false;
    },
    broughtFood() {
      console.log(
        'computed bringFoodOrNot running. value: ',
        this.$store.state.trustee.activeDataEntryObject.broughtFood
      );

      return this.$store.state.trustee.activeDataEntryObject.broughtFood;
    },
    buttonTitle() {
      if (this.selectedTab === 7) {
        return this.$t('mealDataEntry.finish');
      }
      return this.$t('mealDataEntry.next');
    },
    fetchDataForFoodList() {
      if (this.selectedTab > 1 && this.selectedTab < 7) {
        return this.selectedTab;
      }
    }
  },
  watch: {
    selectedTab() {
      if (this.selectedTab === 7) {
        this.summaryTabInit();
      }
    }
  },
  methods: {
    showParentSpinner() {
      this.$parent.$parent.showSpinner();
    },
    hideParentSpinner() {
      setTimeout(() => {
        this.$parent.$parent.hideSpinner();
      }, 500);
    },
    summaryTabInit() {
      let isCombo = mealEntryDataLogic.isCombo(
        false,
        this.studentReportData.items
      );
      let isSuperCombo = mealEntryDataLogic.isCombo(
        true,
        this.studentReportData.items
      );
      console.log('Combo?: ' + isCombo);
      console.log('SuperCombo?: ' + isSuperCombo);
      let encourageObj = mealEntryDataLogic.encouragementSloganGenerator(
        isCombo,
        isSuperCombo,
        this.aggregatedScore,
        this.$t('slogans')
      );
      this.encouragementSlogan = encourageObj.slogan;
      this.encouragementImage = encourageObj.img;
      this.improvementSlogan = mealEntryDataLogic.matchSlogan(
        this.studentReportData.items
      );
    },
    selectRadioOptionBringFood(didBringFood) {
      if (didBringFood) {
        this.didBringFood = true;
        this.didNotBringFood = false;
        this.$store.commit('trustee/setBroughtFood', true);
      } else {
        this.didBringFood = false;
        this.didNotBringFood = true;
        this.$store.commit('trustee/setBroughtFood', false);
        this.$store.commit('trustee/clearItemsArrays');
      }
    },
    async setSelectedTab(tab) {
      if (tab > 1) {
        if (this.didBringFood) {
          if (tab === 7) {
            if (this.selectedSomeItems) {
              // Send data to server here
              spinnerService.startSpinner();

              let res = await this.submitMealDataEntry();

              spinnerService.stopSpinner();
              // Data returned from server
              this.selectedTab = 7;
            } else {
              let confirmUserBroughtFoodButNoItems = confirm(
                this.$t('mealDataEntry.noItemsAddedForStudentConfirm')
              );
              if (confirmUserBroughtFoodButNoItems) {
                // Send data to server here
                spinnerService.startSpinner();

                let res = await this.submitMealDataEntry();

                spinnerService.stopSpinner();
                // Data returned from server
                this.selectedTab = 7;
              } else {
                return;
              }
            }
          }
          this.selectedTab = tab;
        } else {
          if (this.choosedBroughtFood) {
            let confirmUserDidntBroughtFood = confirm(
              this.$t('mealDataEntry.studentDidntBroughtFoodConfirm')
            );
            if (confirmUserDidntBroughtFood) {
              // Send data to server here
              spinnerService.startSpinner();

              let res = await this.submitMealDataEntry();

              spinnerService.stopSpinner();
              // Data returned from server
              this.selectedTab = 7;
            } else {
              return;
            }
          } else {
            alert(this.$t('mealDataEntry.chooseBroughtOrNotConfirm'));
          }
        }
      } else {
        this.selectedTab = tab;
      }
    },
    async submitMealDataEntry() {
      let foodItemsStrings = [];
      for (let category in this.studentReportData.items) {
        for (let item in this.studentReportData.items[category]) {
          foodItemsStrings.push(
            this.studentReportData.items[category][item].itemCode
          );
        }
      }
      let mealDataEntry = {
        learnedDate: new Date(),
        didBringFood: this.broughtFood,
        foodItemsBrought: foodItemsStrings,
        score: this.aggregatedScore,
        combo: mealEntryDataLogic.isCombo(false, this.studentReportData.items),
        superCombo: mealEntryDataLogic.isCombo(
          true,
          this.studentReportData.items
        )
      };
      let response = await trusteeService.insertMealDataEntry(
        this.$store.getters.getToken,
        this.studentReportData.studentId,
        mealDataEntry
      );
      console.log(response);
      return response;
    },
    nextTab() {
      if (this.selectedTab != 7) {
        this.setSelectedTab(this.selectedTab + 1);
      }

      if (this.selectedTab === 7) {
        // Clear DataEntryObject in store
        this.$store.commit('trustee/clearActiveDataEntryObject');
        // Go back to student selection page
        this.$router.push('/Trustee/SelectStudent');
      }
    }
  }
};
</script>

<style>
@font-face {
  font-family: 'OpenSansHebrew-Bold';
  src: url('../../assets/fonts/OpenSansHebrew-Bold.ttf');
  font-weight: Bold;
}

@font-face {
  font-family: 'OpenSansHebrew-Regular';
  src: url('../../assets/fonts/OpenSansHebrew-Regular.ttf');
  font-weight: normal;
}

.meal-data-entry {
  padding-top: 90px;
  font-family: 'OpenSansHebrew-Regular';
  height: 100vh;
}

.main-view-header {
  padding-top: 90px;
  font-family: 'OpenSansHebrew-Bold';
}

.tabs {
  display: inline-flex;
  direction: rtl;
  margin-top: 15px;
  border-bottom: 1px solid #4cdef6;
}

.tab {
  padding: 8px;
  display: inline-grid;
  /* border-bottom: 1px solid #4cdef6; */
  font-size: 9pt;
  color: black;
}

.selected-tab {
  border-bottom: 3px solid #4cdef6;
  margin-bottom: -3px;
  color: #4cdef6;
  font-family: 'OpenSansHebrew-Bold';
}

.disabled-tab {
  color: rgb(122, 122, 122);
}

.disabled-btn {
  transition: 0.5s;
  background-color: rgb(199, 199, 199) !important;
}

span#title {
  margin-top: 5px;
}

.selected-bottom {
  height: 1px;
  width: 81%;
  margin: 0 auto;
  display: flex;
}

.tab-bottom {
  background-color: transparent;
  height: 2px;
  width: 50px;
}

.tab-bottom-active {
  background-color: #4cdef6;
  height: 2px;
  width: 50px;
}

.next-btn-container {
  bottom: 0;
  position: absolute;
  width: 100%;
  padding: 10px 10px 20px 10px;
}

.next-btn-container button {
  transition: 0.5s;
  font-family: 'OpenSansHebrew-Regular';
  font-size: 10pt;
  background-color: #4cdef6;
  border-radius: 45px;
  display: inline-block;
  padding: 15px 50px 15px 50px;
  box-shadow: 2px 2px 30px -10px;
  cursor: pointer;
}

.tab-content {
  margin-top: 15px;
  /* padding: 10px; */
}

.tab-7 .main-tab-content-title {
  margin-top: 20px;
  font-family: 'OpenSansHebrew-Bold';
  font-size: 14pt;
}

.radio-choice-pic {
  margin: 0 auto;
}

.radio-choice-main-div {
  margin-top: 50px;
  display: inline-flex;
}

.radio-choice-div {
  display: grid;
}

.radio-choice-option-title {
  font-family: 'OpenSansHebrew-Bold';
}

.radio-choince-option-points {
  font-size: 9pt;
  margin: 8px;
}

.encouragement-slogan {
  margin-top: 30px;
  margin-bottom: 20px;
}

.improvement-slogan {
  margin: 20px;
}

.inside-tab-padding {
  padding: 10px;
}

.transparent {
  opacity: 0;
}
</style>
