<template>
  <div class="select-student">
    <span class="main-title">{{ $t('selectStudent.mainTitle') }}</span>
    <span class="secondary-title">{{ $t('selectStudent.subTitle') }}</span>
    <div class="students-list">
      <div
        v-for="student in students"
        :key="student.id"
        class="single-student"
        dir="rtl"
        @click="startMealDataEntryProccess(student)"
      >
        <span id="last-name">{{ student.lastName }}</span>
        <span id="first-name">{{ student.firstName }}</span>
        <span id="score">{{ student.score === 0 ? $t('selectStudent.noScoreTitle') : student.score + $t('selectStudent.points') }}</span>
      </div>
    </div>
    <div class="close-btn">{{ $t('close') }}</div>
  </div>
</template>

<script>
import trusteeService from '../../services/trustee-service';

export default {
  name: 'SelectStudent',
  data() {
    return {
      students: [],
      lessonNumber: this.$store.getters['trustee/getMealEntryLessonNumber']
    };
  },
  created() {
    this.getStudentList();
  },
  methods: {
    getStudentList: async function() {
      let token = this.$store.getters.getToken;
      let classId = this.$store.getters['trustee/getActiveSchoolClass'].classId;
      let studentsList = await trusteeService.getStudentListForMealDataEntry(
        token,
        classId
      );
      this.students = studentsList;
    },
    startMealDataEntryProccess: async function(student) {
      console.log(student.id);
      let studentData = {
        id: student.id,
        firstName: student.firstName,
        lastName: student.lastName,
        score: student.score
      };
      this.$store.commit('trustee/setMealEntryStudnetDetails', studentData);
      console.log('pushing meal data entry page');
      this.$router.push('/Trustee/MealDataEntry');
    }
  }
};
</script>

<style>
@font-face {
  font-family: 'OpenSansHebrew-Bold';
  src: url('../../assets/fonts/OpenSansHebrew-Bold.ttf');
  font-weight: Bold;
}

@font-face {
  font-family: 'OpenSansHebrew-Regular';
  src: url('../../assets/fonts/OpenSansHebrew-Regular.ttf');
  font-weight: normal;
}

.select-student {
  padding-top: 90px;
  font-family: 'OpenSansHebrew-Regular';
}

.main-title {
  font-family: 'OpenSansHebrew-Bold';
  display: inherit;
  margin-bottom: 6px;
}

.secondary-title {
  font-size: 10pt;
}

.students-list {
  margin-top: 20px;
  height: 65vh;
}

.single-student {
  border-bottom: 1px solid #b7b7b7;
  text-align: right;
  margin-right: 15px;
  margin-left: 15px;
}

.single-student span {
  padding: 20px 10px 20px 10px;
  font-size: 10pt;
}

span#last-name {
  width: 33%;
  display: inline-block;
}

span#first-name {
  width: 33%;
  display: inline-block;
}

span#score {
  width: 33%;
  display: inline-block;
  font-family: 'OpenSansHebrew-Bold';
}

.close-btn {
  background-color: #4cdef6;
  display: inline-block;
  padding: 15px 60px 15px 60px;
  border-radius: 30px;
  box-shadow: 2px 2px 30px -10px;
  font-size: 10pt;
  margin-bottom: 10px;
  margin-top: 10px;
}
</style>
