<template>
  <div v-if="lessonAgendas">
    <div
      v-for="lesson in lessonAgendas"
      :key="lesson.lessonNumber"
      class="items-container"
    >
      <div
        class="single-lesson"
        dir="rtl"
      >
        <div
          v-if="lesson.lastLesson && isTrustee"
          class="last-or-next-lesson"
        >השיעור האחרון שהועבר</div>
        <div
          v-if="lesson.nextLesson && isTrustee"
          class="last-or-next-lesson"
        >השיעור שמועבר היום</div>
        <div class="lesson-number">{{ lesson.lessonNumber }}</div>
        <span class="lesson-title">{{ lesson.title }}</span>
        <div
          :class="[selectedLesson === lesson.lessonNumber ? 'unlimited-content' : 'limited-content']"
          class="lesson-content-container"
        >
          <p
            class="lesson-content"
            v-html="lesson.contentHtml"
          />
          <div
            v-if="isTrustee"
            class="btn-container"
          ><div
            class="start-reporting-btn"
            @click="procceedToStudentSelect(lesson.lessonNumber)"
          >מה הביאו לאכול היום?</div></div>
        </div>
        <div class="read-more-btn"><img
          class="more-btn"
          src="../assets/img/down.png"
          width="15px"
          :style="[selectedLesson === lesson.lessonNumber ? {'transform': 'rotate(180deg)'} : {'transform': 'rotate(0deg)'}]"
          @click="toggleCard(lesson.lessonNumber)"
        ></div>
      </div>
    </div>
  </div>
</template>


<script>
import trusteeService from '../services/trustee-service';
import teacherService from '../services/teacher-service';
import { setTimeout } from 'timers';
import spinnerService from '../services/spinner-service';

export default {
  name: 'LessonAgendasComponent',
  props: { isTrustee: Boolean },
  data() {
    return {
      lessonAgendas: [],
      selectedLesson: 0
    };
  },
  created() {
    this.getLessonAgendas();
  },
  methods: {
    getLessonAgendas: async function() {
      let token = this.$store.getters.getToken;
      let lessons = null;
      if (this.isTrustee) {
        let classId = this.$store.getters['trustee/getActiveSchoolClass']
          .classId;
        lessons = await trusteeService.getTrusteeAgendaLessonsByClassId(
          token,
          classId
        );
      } else {
        lessons = await teacherService.getLessonAgendas(token);
      }

      this.lessonAgendas = lessons;
    },
    toggleCard(lesNum) {
      if (this.selectedLesson == lesNum) {
        this.selectedLesson = 0;
      } else {
        this.selectedLesson = lesNum;
      }
    },
    async procceedToStudentSelect(selectedAgendaLesson) {
      spinnerService.startSpinner();
      let data = {
        classId: this.$store.getters['trustee/getActiveSchoolClass'].classId,
        learnedLesson: selectedAgendaLesson
      };
      let response = await trusteeService.insertNewLearnedLesoon(
        this.$store.getters.getToken,
        data
      );
      console.log(response);
      if (response.success) {
        this.$store.commit('trustee/clearMetalEntryStudentObject');
        console.log(
          'Selceted lesson number: ' +
            selectedAgendaLesson +
            ' to start mealEntryProccess with.'
        );
        this.$store.commit(
          'trustee/setMealEntryLessonNumber',
          selectedAgendaLesson
        );
        spinnerService.stopSpinner();
        this.$router.push('/Trustee/SelectStudent');
      } else {
        spinnerService.stopSpinner();
        alert('Error occured on server request. Try again.');
      }
    }
  }
};
</script>

<style>
@font-face {
  font-family: 'OpenSansHebrew-Bold';
  src: url('../assets/fonts/OpenSansHebrew-Bold.ttf');
  font-weight: Bold;
}

@font-face {
  font-family: 'OpenSansHebrew-Regular';
  src: url('../assets/fonts/OpenSansHebrew-Regular.ttf');
  font-weight: normal;
}

.main-header {
  margin-top: 10px;
  font-family: 'OpenSansHebrew-Bold';
}

.lesson-agends {
  background-color: #edfcff;
}

.single-lesson {
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 20px;
  box-shadow: 3px 3px 30px -13px;
  border-radius: 8px;
  /* max-height: 100px; */
  overflow: hidden;
  background-color: white;
  text-align: right;
  padding: 15px;
}

.lesson-title {
  font-family: 'OpenSansHebrew-Bold';
  font-size: 10pt;
  margin-right: 5px;
}

.lesson-number {
  font-family: 'OpenSansHebrew-Bold';
  font-size: 10pt;
  display: inline-block;
  background: #4cdef6;
  border-radius: 70%;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 7px;
  padding-left: 5px;
}

.lesson-content {
  font-family: 'OpenSansHebrew-Regular';
  font-size: 10pt;
  padding: 15px 20px 0px 20px;
  overflow: hidden;
  line-height: 20px;
}

.start-reporting-btn {
  font-family: 'OpenSansHebrew-Regular';
  font-size: 10pt;
  background-color: #4cdef6;
  border-radius: 45px;
  display: inline-block;
  padding: 15px 40px 15px 40px;
  box-shadow: 2px 2px 30px -10px;
  cursor: pointer;
}

.btn-container {
  text-align: center;
  margin-bottom: 20px;
}

.limited-content {
  transition: max-height 0.15s ease-out;
  max-height: 60px;
  background: -webkit-linear-gradient(rgb(26, 26, 26), rgb(238, 238, 238));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  user-select: none;
  pointer-events: none;
}

.unlimited-content {
  transition: max-height 0.25s ease-in;
  max-height: none;
}

.more-btn {
  margin: 0 auto;
}

.last-or-next-lesson {
  font-family: 'OpenSansHebrew-Regular';
  position: absolute;
  left: 0;
  margin-left: 8px;
  background-color: #ff005d;
  color: white;
  font-size: 8pt;
  padding: 5px 15px 5px 12px;
  -webkit-clip-path: polygon(0% 0%, 90% 0%, 100% 50%, 90% 100%, 0% 100%);
  /* clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%); */
}

.items-container {
  margin-bottom: 20px;
}
</style>
