import axios from 'axios';
import apiConfig from './app-api';
import stubs from '../util/stubs';
import Axios from 'axios';

export default {
  getStudentListForMealDataEntry: async function(token, classId) {
    const response = await axios.post(
      apiConfig.getStudentListForMealDataEntryUrl(),
      { classId },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': token
        }
      }
    );
    return response.data.lessons;
  },
  getTrusteeAgendaLessonsByClassId: async function(token, classId) {
    const response = await axios.post(
      apiConfig.getLessonAgendasByClassIdUrl(),
      { classId },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': token
        }
      }
    );
    return response.data.lessons;
  },
  getFoodCatalog: async function(token) {
    const response = await axios.get(apiConfig.getFoodCatalogUrl(), {
      headers: {
        'Content-Type': 'application/json',
        'x-access-token': token
      }
    });
    return response.data.catalog;
  },
  insertNewLearnedLesoon: async function(token, data) {
    const response = await axios.post(
      apiConfig.getInsertNewLearnedLessonUrl(),
      { ...data },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': token
        }
      }
    );
    return response.data;
  },
  insertMealDataEntry: async function(token, studentId, dataEntry) {
    const response = await axios.post(
      apiConfig.getSubmitNewMealDataEntryUrl(),
      { dataEntry, studentId },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': token
        }
      }
    );
    await stubs.stubDelay();
    return response.data;
  },
  mapSchoolClassesToHeb: schoolClasses => {
    // debugger;
    return schoolClasses.map(schoolClass => ({
      ...schoolClass,
      schoolClassName:
        grades[schoolClass.classGrade] + "' " + schoolClass.classNumber
    }));
  }
};
