<template>
  <div class="food-list-component">
    <div
      class="component-title"
      dir="rtl"
    >{{ getCurrentListData.title }}
    </div>
    <div class="food-list-items">
      <div
        v-for="item in getCurrentListData.items"
        :key="item.itemCode"
        class="food-item"
        dir="rtl"
      >
        <div class="item-img-container">
          <img
            :src="!doesItemExistInUserCart(item) ? require('../assets/img/mealdataentry/food-catalog-icons/' + item.category + '/' + item.itemCode + '.png') : 
            require('../assets/img/mealdataentry/food-catalog-icons/' + item.category + '/' + item.itemCode + '_SELECTED.png')"
            class="item-img"
          >
        </div>
        <div class="item-name-and-score">
          <div class="item-name">{{ item.itemName }}</div>
          <br>
          <div class="item-score">{{ item.itemScore }} {{ $t('mealDataEntry.points') }}</div>
        </div>
        <div
          class="add-remove-item-btn"
        >
          <img
            v-if="!doesItemExistInUserCart(item)"
            src="../assets/img/mealdataentry/add-item.png"
            class="add-btn"
            @click="addItemToDataEntryObject(item)"
          >
          <img
            v-if="doesItemExistInUserCart(item)"
            src="../assets/img/mealdataentry/remove-item.png"
            class="add-btn"
            @click="removeItemFromDataEntryObject(item)"
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash';
import trusteeService from '../services/trustee-service';

export default {
  name: 'FoodList',
  props: { selectedTab: { type: Number, default: null } },
  data() {
    return {
      foodCatalog: null
    };
  },
  computed: {
    itemListFromStore() {
      return this.$store.getters['trustee/getActiveDataEntryItems'];
    },
    getCurrentListData: function() {
      if (this.selectedTab > 1 && this.selectedTab < 7) {
        let catalogCode = this.getStrByTabNum(this.selectedTab);
        let currentFoodCatalogArray = this.foodCatalog[catalogCode];
        let currentTitle = this.getTitleByCatalogCode(catalogCode);
        let dataObject = {
          title: currentTitle,
          items: currentFoodCatalogArray
        };
        console.log(dataObject);
        return dataObject;
      } else {
        return { title: '', items: null };
      }
    }
  },
  watch: {
    itemListFromStore: {
      handler() {
        console.log('Object CHANGED');
        console.log(this.itemListFromStore);
      },
      deep: true
    }
  },
  created() {
    this.getFoodCatalog();
  },
  methods: {
    doesItemExistInUserCart(item) {
      if (this.itemListFromStore) {
        let isExist = _.find(this.itemListFromStore[item.category], {
          itemCode: item.itemCode
        });
        return isExist;
      } else {
        return false;
      }
    },
    getFoodCatalog: async function() {
      console.log('getFoodCatalog!');
      let token = this.$store.getters.getToken;
      let catalog = await trusteeService.getFoodCatalog(token);
      if (catalog) {
        this.foodCatalog = catalog;
        console.log(catalog);
      }
    },
    getStrByTabNum(selectedTab) {
      const tabNumStrArr = [
        { num: 2, str: 'BR' },
        { num: 3, str: 'IN' },
        { num: 4, str: 'VG' },
        { num: 5, str: 'FR' },
        { num: 6, str: 'OT' }
      ];
      let selectedTabStr = tabNumStrArr.find(
        tabNum => tabNum.num === selectedTab
      );
      return selectedTabStr.str;
    },
    getTitleByCatalogCode(catalogCode) {
      let selectedTabTitle = this.$t('mealDataEntry.categories.' + catalogCode);
      return selectedTabTitle;
    },
    addItemToDataEntryObject(item) {
      this.$store.commit(
        'trustee/addItemToActiveDataEntryItemsByCategory',
        item
      );
    },
    removeItemFromDataEntryObject(item) {
      this.$store.commit(
        'trustee/removeItemFromActiveDataEntryItemsByCategory',
        item
      );
    }
  }
};
</script>

<style>
.food-item {
  display: flex;
  border-bottom: 1px solid #b7b7b7;
  padding: 5px;
  margin-left: 5px;
  margin-right: 5px;
}

img.item-img {
  height: 50px;
}

.item-name-and-score {
  text-align: right;
  line-height: 50px;
  width: 55%;
}

div .item-name-and-score {
  line-height: 15px;
}

.item-name {
  margin-top: 10px;
  font-family: 'OpenSansHebrew-Bold';
  font-size: 9pt;
  display: inline-block;
}

.item-score {
  margin-bottom: 10px;
  font-size: 8pt;
  display: inline-block;
}

.add-remove-item-btn {
  /* left: 0; */
  width: 20%;
}

img.add-btn {
  height: 20px;
  margin: 15px;
  float: left;
}

.food-list-items {
  margin-top: 40px;
  max-height: 55vh;
  overflow: auto;
}

.item-img-container {
  width: 25%;
}
</style>
