<template>
  <div class="contact">
    <!-- <br> -->
    <div class="top-marg main-top-marg">
      <div class="container-form">
        <h1> {{ $t('contact') }} </h1>
        <br>
        <div
          v-show="sendMailMode === 0 || sendMailMode === 500"
          class="top-marg"
        > 
          <!-- this div contains all the form, including the submit btn -->
          <div class="flex space-around flex-wrap contact-form">
            <!-- <div> -->
            <div class="input-wrapper">
              <v-text-field
                v-model="formData.fullName"
                class="field"
                :label="$t('labels.fullName')"
                :reverse="true"
                :rules="[rules.required]"
                @click:append="touched.name = !touched.name"
              />
              <span class="my-validation-text"> נא להזין שם מלא</span>
            </div> 
            <div class="input-wrapper">
              <v-text-field
                v-model="formData.phone"
                class="field"
                :label="$t('labels.phone')"
                :reverse="true"
                :rules="[rules.required]"
                @click:append="touched.phone = !touched.phone"
              />
              <span class="my-validation-text"> נא להזין מס' טלפון חוקי</span>
            </div>
            <div class="input-wrapper">
              <v-text-field
                v-model="formData.email"
                class="field input-group--focused"
                :label="$t('labels.email')"
                :reverse="true"
                :rules="[rules.required, rules.email]"
                @click:append="touched.email = !touched.email"
              />
              <span class="my-validation-text"> נא להזין מייל חוקי</span>
            </div>

            <!-- <input 
              v-model="formData.fullName" 
              class="field" 
              type="text" 
              placeholder="שם מלא"
              required
              > -->
            <!-- <span class="floating-label">שם מלא</span> -->
            <!-- </div>  -->
            <!-- <input 
              v-model="formData.phone" 
              class="field" 
              type="text" 
              placeholder="טלפון"
            > -->
            <!-- <input 
              v-model="formData.lastName" 
              class="field" 
              type="text" 
              placeholder="שם משפחה"
            > -->
            <!-- <input 
              v-model="formData.mail" 
              class="field" 
              type="email" 
              placeholder="מייל"
            > -->
          </div>
          <div class="top-marg">
            <div class="flex space-around flex-wrap contact-form">
              <div class="input-wrapper textarea">
                <v-textarea
                  v-model="formData.message"
                  :reverse="true"
                  :label="$t('labels.message')"
                  :auto-grow="true"
                  rows="1"
                  :rules="[rules.required]"
                  @click:append="touched.message = !touched.message"
                  @input="validateFormFields()"
                />
                <span class="my-validation-text"> נא להזין תוכן להודעה</span>
              </div>
            </div>
            <!-- <input 
              v-model="formData.subject" 
              class="field-subjet" 
              type="text" 
              placeholder="נושא"
            > -->
            <!-- <br><br> -->
            <!-- <textarea 
              v-model="formData.message" 
              class="field-message" 
              type="text" 
              placeholder="תוכן ההודעה"
            /> -->
          </div>
          <div class="top-marg-media">
            <button 
              class="btn-form" 
              :disabled="!isFormValid"
              :class="{ 'disabled': !isFormValid, 'pointer': isFormValid }"
              @click="sendMail"
            > {{ $t('send') }}</button>
          </div>
        </div>

        <div v-show="sendMailMode === 100">
          <Spinner spinner="2" />
        </div>

        <div
          v-show="sendMailMode === 200"
          class="top-marg-media margin-bot success-container"
        >
          <div>
            <img
              src="../assets/img/sandwich2.png"
              alt=""
            >
          </div>
          <div>
            <p>
              {{ $t('sentSuccessfully') }}
            </p>
          </div>
          <div>
            <button 
              class="btn-form"
              @click="navigateBack"
            > {{ $t('confirm') }}</button>
          </div>
        </div>

        <div class="social top-marg-media">
          <div>
            <h5> {{ $t('keepInTouch') }} </h5>
          </div>
          <div class="flex justify-center social-icons">
            <span>
              <img
                src="../assets/img/facebook.png"
                class="icon pointer"
                alt=""
              >
            </span>
            <span>
              <img
                src="../assets/img/website.png"
                class="icon  pointer"
                alt=""
              >
            </span>
            <span>
              <img
                src="../assets/img/mail.png"
                class="icon  pointer"
                alt=""
              >
            </span>
            <span>
              <img
                src="../assets/img/youtube.png"
                class="icon  pointer"
                alt=""
              >
            </span>
          </div>
          <!-- <button class="btn-social">Y</button>
          <button class="btn-social">
            <span class="btn-social">
              <i class="material-icons">
            mail_outline
            </i>
            </span>
            
          </button>
          <button class="btn-social">W</button> -->
        </div>
      </div>
    </div>

  </div>
</template>

<script>
// import NavBar from '@/components/NavBar.vue';
import Spinner from '@/components/Spinner.vue';
import generalService from '../services/general-service';
import { log } from 'util';
import { setTimeout } from 'timers';
import Notifications from '../services/notifications';

export default {
  name: 'Login',
  components: {
    // NavBar,
    Spinner
  },
  data() {
    return {
      formData: {
        fullName: null,
        phone: null,
        // lastName: null,
        email: null,
        // subject: null,
        message: null
      },
      sendMailMode: 500, // help the component know when the mail is being sent, failed or not initiated.
      //values: 0 = not initiated, 100 = being sent, 200 = sent successfully, 500 = fail
      // isFormValid: false,
      rules: {
        required: value => !!value || '',
        email: () => this.email || ''
      },
      touched: {
        email: false,
        phone: false,
        name: false,
        message: false
      }
    };
  },
  computed: {
    email() {
      return this.validEmail(this.formData.email);
    },
    isFormValid() {
      return this.validateFormFields();
    }
  },
  methods: {
    validateFormFields: function() {
      for (let key in this.formData) {
        if (!this.formData[key]) {
          return false;
        }
        if (key === 'email') {
          if (!this.validEmail(this.formData[key])) {
            console.log('Mail is not valid');
            return false;
          }
        }
      }
      return true;
    },
    validEmail: function(email) {
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    },
    sendMail: async function() {
      let checkedFormData = this.validateFormFields();
      if (checkedFormData) {
        // if ( false ) { // for testing
        // Form data is valid, initialize a request to the server
        let formattedMailData = {
          email: checkedFormData.email,
          phone: checkedFormData.phone,
          name: checkedFormData.fullName, // + ' ' + checkedFormData.lastName,
          // subject: checkedFormData.subject,
          content: checkedFormData.message
        };
        this.sendMailMode = 100;
        let response = await generalService.sendContactUsMail(
          formattedMailData
        );
        if (response.success) {
          console.log('Mail sent successfully.');
          // setTimeout(()=>
          this.sendMailMode = 200;
          // , 1000)
        } else {
          console.log('Mail request failed.');
          this.sendMailMode = 500;
        }
      } else {
        // Form data didn't pass the test function
        console.log('invalid form');
        Notifications.noTitle('warn', this.$t('serverWarn'));
        this.sendMailMode = 500;
      }
    },
    navigateBack() {
      //navigates to last location.
      this.$router.back();
    }
  }
};
</script>

 <style lang="scss">
@import '../assets/scss/style.scss';

.main-top-marg.top-marg {
  margin-top: 3rem;
}

.input-wrapper {
  position: relative;
}

.input-wrapper.textarea {
  width: 92%;
}

input {
  padding: 5px 10px 5px 10px;
  border-top: none;
  border-right: none;
  border-left: none;
  background: transparent;
  outline: none;
}

.contact-form {
  direction: rtl;
}

// .field-subjet {
//   width: 95%;
// }

// textarea {
//   outline: none;
//   direction: rtl;
//   padding: 5px 10px 5px 10px;
//   border-top: none;
//   border-right: none;
//   border-left: none;
//   border-bottom: 1px solid black;
//   background: transparent;
// }

.field {
  min-width: 200px;
  margin-bottom: 1rem;
}

.container-form {
  padding: 1rem;
  background-color: #ffffff;
  margin: 0 auto;
  width: 100%;
  max-width: 820px;
  box-shadow: 3px 3px 15px -3px rgba(0, 0, 0, 0.7);
  transition: 0.3s;

  .btn-form {
    border: none;
    border-radius: 15px;
    // background-color: rgb(194, 194, 194);
    background-color: $btns;
    padding: 0.5rem 1rem 0.5rem 1rem;
  }

  .btn-form.disabled {
    background-color: #cccccc;
  }
}

.social {
  margin-top: 2rem;
  .material-icons {
    line-height: 1.15;
  }
  .btn-social {
    cursor: pointer;
    padding: 0.5rem;
    margin: 0.5rem;
    height: 2em;
    width: 2em;
    background-color: $btns;
    border-radius: 100%;
    border: none;
  }

  .social-icons span {
    padding: 1.5em 0.7em;
  }

  img {
    // box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
    filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.15));
  }
}

.field-message {
  height: auto;
  width: 95%;
  font-size: 14px;
}

h1 {
  color: $btns;
}

//form err css
.error--text ~ .my-validation-text {
  color: red;
  opacity: 1;
}

.my-validation-text {
  position: absolute;
  font-size: 12px;
  left: 0;
  top: 4%;
  opacity: 0;
  transition: 0.3s;
}

.material-icons {
  vertical-align: middle;
}

.success-container {
  img {
    margin-left: auto;
    margin-right: auto;
  }

  p {
    margin-top: 1.5em;
  }
}

@media (max-width: 720px) {
  .container-form {
    height: 90vh;
    max-width: 100%;
    padding: 1rem 2rem 1rem 2rem;
  }

  .field {
    min-width: 200px;
    width: 100%;
  }

  .field-message {
    min-width: 200px;
    width: 100%;
  }

  h1 {
    color: black;
    margin: 0;
  }

  .field-subjet {
    width: 100%;
  }

  .main-top-marg.top-marg {
    margin-top: 0;
  }

  .my-validation-text {
    left: 0;
    top: 2%;
  }

  .success-container {
    img {
      // margin-top: 1em;
      margin-top: 3vh;
    }

    p {
      // margin-top: 3em;
      margin-top: 9vh;
    }
  }

  .success-container.margin-bot {
    // margin-bottom: 5em;
    margin-bottom: 15vh;
  }

  .social {
    // margin-bottom:3rem;
    margin-bottom: 9vh;
  }

  .input-wrapper {
    width: 92%;
  }
}

@media (min-width: 720px) {
  .top-marg-media,
  .btn-form {
    margin-top: 2rem;
  }
}

@media (max-height: 400px) {
  .container-form {
    height: 140vh;
  }
}
</style>
