const _ = require('lodash');

const VEGETABLE_ARRAY = 'VG';
const FRUITS_ARRAY = 'FR';
const BREAD_ARRAY = 'BR';
const IN_BREAD_ARRAY = 'IN';
const OTHERS_ARRAY = 'OT';

export default {
  matchSlogan: foodItemsBrought => {
    // Return a translation object name in order to get it with $t
    for (let sloganCheck in improvementSlogansList) {
      if (improvementSlogansList[sloganCheck].func(foodItemsBrought)) {
        return improvementSlogansList[sloganCheck].sloganCode;
      }
    }
    return 'improvementSlogans.defaultSlogan';
  },
  encouragementSloganGenerator: function(
    combo,
    superCombo,
    finalScore,
    slogansObject
  ) {
    let encourageAndImg = null;
    if (finalScore === 0) {
      encourageAndImg =
        slogansObject['slogans0'][
          randomNumber(slogansObject['slogans0'].length)
        ];
    } else if (finalScore === 1) {
      encourageAndImg =
        slogansObject['slogans1'][
          randomNumber(slogansObject['slogans1'].length)
        ];
    } else if (finalScore === 2) {
      encourageAndImg =
        slogansObject['slogans2'][
          randomNumber(slogansObject['slogans2'].length)
        ];
    } else if (finalScore === 3) {
      encourageAndImg =
        slogansObject['slogans3'][
          randomNumber(slogansObject['slogans3'].length)
        ];
    } else if (finalScore >= 4) {
      if (combo) {
        if (superCombo) {
          // 2 Stars
          encourageAndImg =
            slogansObject['slogans4pcc'][
              randomNumber(slogansObject['slogans4pcc'].length)
            ];
        } else {
          // 1 Star
          encourageAndImg =
            slogansObject['slogans4pc'][
              randomNumber(slogansObject['slogans4pc'].length)
            ];
        }
      } else {
        encourageAndImg =
          slogansObject['slogans4p'][
            randomNumber(slogansObject['slogans4p'].length)
          ];
      }
    }

    return encourageAndImg;
  },
  isCombo: function(superCombo = null, itemsSelectedCategories) {
    let broughtSandwich = false;
    let broughtSandwichExtraWorthPoints = false;
    let broughtVegetable = false;
    let broughtFruit = false;

    let breadArray = itemsSelectedCategories['BR'];
    if (breadArray.length > 0) {
      if (superCombo) {
        for (let item in breadArray) {
          if (breadArray[item].itemScore > 0) {
            broughtSandwich = true;
            break;
          }
        }
        if (!broughtSandwich) {
          return false;
        }
      } else {
        broughtSandwich = true;
      }
    } else {
      return false;
    }

    let insideSandwichArray = itemsSelectedCategories['IN'];
    if (insideSandwichArray.length > 0) {
      for (let item in insideSandwichArray) {
        if (insideSandwichArray[item].itemScore > 0) {
          broughtSandwichExtraWorthPoints = true;
          break;
        }
      }
      if (!broughtSandwichExtraWorthPoints) {
        return false;
      }
    }

    let vegetableArray = itemsSelectedCategories['VG'];
    if (vegetableArray.length > 0) {
      broughtVegetable = true;
    } else {
      return false;
    }
    let fruitArray = itemsSelectedCategories['FR'];
    if (fruitArray.length > 0) {
      broughtFruit = true;
    } else {
      return false;
    }

    if (
      broughtSandwich &&
      broughtSandwichExtraWorthPoints &&
      broughtVegetable &&
      broughtFruit
    ) {
      return true;
    }
    return false;
  }
};

const improvementSlogansList = [
  {
    func: hasBroughtEverythingExceptVegetables,
    sloganCode: 'improvementSlogans.hasBroughtEverythingExceptVegetables'
  },
  {
    func: hasBroughtEverythingExceptFruits,
    sloganCode: 'improvementSlogans.hasBroughtEverythingExceptFruits'
  },
  {
    func: hasBroughtEverythingExceptFullWheat,
    sloganCode: 'improvementSlogans.hasBroughtEverythingExceptFullWheat'
  },
  {
    func: hasBroughtEverythingExceptHealthyExtra,
    sloganCode: 'improvementSlogans.hasBroughtEverythingExceptHealthyExtra'
  },
  {
    func: hasBroughtSandwichWithExtraButNoVegOrFruit,
    sloganCode: 'improvementSlogans.hasBroughtSandwichWithExtraButNoVegOrFruit'
  },
  {
    func: hasBroughtOnlySuperHealthySandwich,
    sloganCode: 'improvementSlogans.hasBroughtOnlySuperHealthySandwich'
  },
  {
    func: hasBroughtOnlyVegetables,
    sloganCode: 'improvementSlogans.hasBroughtOnlyVegetables'
  },
  {
    func: hasBroughtOnlyFruits,
    sloganCode: 'improvementSlogans.hasBroughtOnlyFruits'
  },
  {
    func: hasBroughtEverything,
    sloganCode: 'improvementSlogans.hasBroughtEverything'
  },
  {
    func: didntBroughtNothingHealthy,
    sloganCode: 'improvementSlogans.didntBroughtNothingHealthy'
  },
  {
    func: hasBroughtOnlyFullWheat,
    sloganCode: 'improvementSlogans.hasBroughtOnlyFullWheat'
  }
];

function hasBroughtEverythingExceptVegetables(foodItemsBrought) {
  if (foodItemsBrought[VEGETABLE_ARRAY].length > 0) {
    // Did brought vegetables, return false
    return false;
  } else {
    // Didn't brought vegetables, check the rest
    for (let category in foodItemsBrought) {
      if (
        foodItemsBrought[category].length === 0 &&
        category !== VEGETABLE_ARRAY &&
        category !== OTHERS_ARRAY
      ) {
        return false;
      }
    }
    // Check for one point (or more) bread
    let healthyBread = false;
    for (let item in foodItemsBrought[BREAD_ARRAY]) {
      if (foodItemsBrought[BREAD_ARRAY][item].itemScore > 0) {
        healthyBread = true;
      }
    }

    if (healthyBread) {
      // Check for one point (or more) sandwich extra
      let healthySandwichExtra = false;
      for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
        if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
          healthySandwichExtra = true;
        }
      }
      if (healthySandwichExtra) {
        return true;
      }
    } else {
      return false;
    }
  }
  return false;
}

function hasBroughtEverythingExceptFruits(foodItemsBrought) {
  if (foodItemsBrought[FRUITS_ARRAY].length > 0) {
    // Did brought fruits, return false
    return false;
  } else {
    // Didn't brought fruits, check the rest
    for (let category in foodItemsBrought) {
      if (
        foodItemsBrought[category].length === 0 &&
        category !== FRUITS_ARRAY &&
        category !== OTHERS_ARRAY
      ) {
        return false;
      }
    }
    // Check for one point (or more) bread
    let healthyBread = false;
    for (let item in foodItemsBrought[BREAD_ARRAY]) {
      if (foodItemsBrought[BREAD_ARRAY][item].itemScore > 0) {
        healthyBread = true;
      }
    }

    if (healthyBread) {
      // Check for one point (or more) sandwich extra
      let healthySandwichExtra = false;
      for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
        if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
          healthySandwichExtra = true;
        }
      }
      if (healthySandwichExtra) {
        return true;
      }
    } else {
      return false;
    }
  }
  return false;
}

function hasBroughtEverythingExceptFullWheat(foodItemsBrought) {
  if (foodItemsBrought[BREAD_ARRAY].length === 0) {
    // Didn't brought any bread, return false
    return false;
  } else {
    // Brought bread, check if is full wheat
    for (let item in foodItemsBrought[BREAD_ARRAY]) {
      if (foodItemsBrought[BREAD_ARRAY][item].fullWheat) {
        return false;
      }
    }

    // No full wheat item
    for (let category in foodItemsBrought) {
      if (
        foodItemsBrought[category].length === 0 &&
        category !== BREAD_ARRAY &&
        category !== OTHERS_ARRAY
      ) {
        return false;
      }
    }

    // Check for healthy extra
    for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
      if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
        return true;
      }
    }
  }
  return true;
}

function hasBroughtEverythingExceptHealthyExtra(foodItemsBrought) {
  // Check if one of the items in is an healty sandwich extra (points > 0)
  for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
    if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
      return false;
    }
  }

  // No healthy extra, check everything else exist
  for (let category in foodItemsBrought) {
    if (
      foodItemsBrought[category].length === 0 &&
      category !== IN_BREAD_ARRAY &&
      category !== OTHERS_ARRAY
    ) {
      return false;
    }
  }

  //Check for healthy bread
  for (let item in foodItemsBrought[BREAD_ARRAY]) {
    if (foodItemsBrought[BREAD_ARRAY][item].itemScore > 0) {
      return true;
    }
  }

  return false;
}

function hasBroughtSandwichWithExtraButNoVegOrFruit(foodItemsBrought) {
  // First, check for vegetables and fruits, and return false if exist
  if (
    foodItemsBrought[VEGETABLE_ARRAY].length > 0 ||
    foodItemsBrought[FRUITS_ARRAY].length > 0
  ) {
    return false;
  } else {
    // No vegetables or fruits, proceed
    // check if brought any bread
    if (foodItemsBrought[BREAD_ARRAY].length === 0) {
      return false;
    } else {
      // checking if brought bread which is full wheat
      for (let item in foodItemsBrought[BREAD_ARRAY]) {
        if (foodItemsBrought[BREAD_ARRAY][item].fullWheat) {
          return false;
        }
      }
      // No full wheat bread, check for healthy extra
      for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
        if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
          return true;
        }
      }
    }
  }
}

function hasBroughtOnlySuperHealthySandwich(foodItemsBrought) {
  // first, check that didn't brouht anything but bread and extras
  for (let category in foodItemsBrought) {
    if (
      foodItemsBrought[category].length > 0 &&
      category !== BREAD_ARRAY &&
      category !== IN_BREAD_ARRAY
    ) {
      return false;
    }
  }

  // Now, check for full wheat bread
  let isFullWheat = false;
  for (let item in foodItemsBrought[BREAD_ARRAY]) {
    if (foodItemsBrought[BREAD_ARRAY][item].fullWheat) {
      isFullWheat = true;
      break;
    }
  }

  if (isFullWheat) {
    // Brought full wheat bread, check for healthy extra
    for (let item in foodItemsBrought[IN_BREAD_ARRAY]) {
      if (foodItemsBrought[IN_BREAD_ARRAY][item].itemScore > 0) {
        return true;
      }
    }
  }
}

function hasBroughtOnlyVegetables(foodItemsBrought) {
  // First check brought vegetables
  if (foodItemsBrought[VEGETABLE_ARRAY].length === 0) {
    // No vegetables, return false
    return false;
  } else {
    // Vegtables exist, check all other arrays are empty
    for (let category in foodItemsBrought) {
      if (
        foodItemsBrought[category].length > 0 &&
        category !== VEGETABLE_ARRAY
      ) {
        return false;
      }
    }
  }
  return true;
}

function hasBroughtOnlyFruits(foodItemsBrought) {
  // First check brought fruits
  if (foodItemsBrought[FRUITS_ARRAY].length === 0) {
    // No fruits, return false
    return false;
  } else {
    // Fruits exist, check all other arrays are empty
    for (let category in foodItemsBrought) {
      if (foodItemsBrought[category].length > 0 && category !== FRUITS_ARRAY) {
        return false;
      }
    }
  }
  return true;
}

function hasBroughtEverything(foodItemsBrought) {
  if (isCombo(true, foodItemsBrought)) {
    return true;
  }
  return false;
}

function didntBroughtNothingHealthy(foodItemsBrought) {
  // Iterate over all, and find something healthy
  // let itemsExist = false;
  for (let category in foodItemsBrought) {
    if (foodItemsBrought[category].length > 0) {
      // itemsExist = true;
      for (let item in foodItemsBrought[category]) {
        if (foodItemsBrought[category][item].itemScore > 0) {
          return false;
        }
      }
    }
  }
  // if (!itemsExist) {
  //   return false;
  // }

  return true;
}

function hasBroughtOnlyFullWheat(foodItemsBrought) {
  // first, check for full wheat item
  let isFullWheat = false;
  for (let item in foodItemsBrought[BREAD_ARRAY]) {
    if (foodItemsBrought[BREAD_ARRAY][item].fullWheat) {
      isFullWheat = true;
      break;
    }
  }

  if (isFullWheat) {
    // Now check no other items
    for (let category in foodItemsBrought) {
      if (foodItemsBrought[category].length > 0 && category !== BREAD_ARRAY) {
        return false;
      }
    }
  } else {
    return false;
  }

  return true;
}

function randomNumber(maxNum) {
  return _.random(0, maxNum - 1);
}

function isCombo(superCombo = null, itemsSelectedCategories) {
  let broughtSandwich = false;
  let broughtSandwichExtraWorthPoints = false;
  let broughtVegetable = false;
  let broughtFruit = false;

  let breadArray = itemsSelectedCategories['BR'];
  if (breadArray.length > 0) {
    if (superCombo) {
      for (let item in breadArray) {
        if (breadArray[item].itemScore > 0) {
          broughtSandwich = true;
          break;
        }
      }
      if (!broughtSandwich) {
        return false;
      }
    } else {
      broughtSandwich = true;
    }
  } else {
    return false;
  }

  let insideSandwichArray = itemsSelectedCategories['IN'];
  if (insideSandwichArray.length > 0) {
    for (let item in insideSandwichArray) {
      if (insideSandwichArray[item].itemScore > 0) {
        broughtSandwichExtraWorthPoints = true;
        break;
      }
    }
    if (!broughtSandwichExtraWorthPoints) {
      return false;
    }
  }

  let vegetableArray = itemsSelectedCategories['VG'];
  if (vegetableArray.length > 0) {
    broughtVegetable = true;
  } else {
    return false;
  }
  let fruitArray = itemsSelectedCategories['FR'];
  if (fruitArray.length > 0) {
    broughtFruit = true;
  } else {
    return false;
  }

  if (
    broughtSandwich &&
    broughtSandwichExtraWorthPoints &&
    broughtVegetable &&
    broughtFruit
  ) {
    return true;
  }
  return false;
}
